@import url(https://fonts.googleapis.com/css?family=Raleway|Roboto&display=swap);
h1, h2, h3, h4, p {
  margin: 0;  
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  margin: auto;
  /* background-color: #f5f5f5; */
  font-family: 'Roboto', sans-serif;
}

.App .body {
  margin: auto;
}

@media (min-width: 768px) {
  .App .body {
    width: 60%;
  }
}
@media (max-width: 767px) {
  .App .body {
    width: 100%;
  }
}

.content {
  padding: 40px;
  min-height: 100vh;
  font-family: 'Raleway', sans-serif;
  padding-top: 100px;
}

.footer {
  padding: 10px;
  background-color: #59595B;
  color: white;
  text-align: right;
}
.navbar {
    background-color: #2a2e45;
    /* min-height: 5vh; */
    display: grid;
    grid-template-columns: 1fr 14fr;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    /* color: cadetblue; */
    color: white;
    padding-top: 12px;
    padding-bottom: 12px;
    position: fixed;
    width: 100%;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 2px 6px 0 rgba(0, 0, 0, 0.19);
}

.navbar .logo {
    display: grid;
    margin-right: 5vh;
    margin-left: 5vh;
    font-size: 32px;
    font-weight: 800;
}

.navbar a {
    text-decoration: none;
    color: grey;
    font-size: 80%;
}

.navbar a:hover, .navbar a.selected {
    color: white;
}

.navbar li {
    display: inline;
    list-style-type: none;
    padding-right: 20px;
}


.navbar ul {
    margin: 0px;
    padding: 0;
}
